import { useParams, useSearchParams } from '@remix-run/react';
import { useMemo } from 'react';

export default function useGenerateSearchParams(modifyKeys?: {
  keysToRemove?: string[] | string;
  keysToAdd?: Record<string, string>;
}) {
  const [searchParams] = useSearchParams();
  const params = useParams();


  return useMemo( () => {
    const newSearchParams = new URLSearchParams();
    for ( const entry of searchParams.entries() ) {
      if ( Array.isArray( modifyKeys?.keysToRemove ) ) {
        console.log( modifyKeys.keysToRemove?.includes( entry[0] ) );
        if ( entry[0] !== 'page' && !modifyKeys.keysToRemove?.includes( entry[0] ) ) {
          newSearchParams.set( entry[0], entry[1] );
        }
      }
      else {
        if ( entry[0] !== 'page' && modifyKeys?.keysToRemove !== entry[0] ) {
          newSearchParams.set( entry[0], entry[1] );
        }
      }
    }

    if ( modifyKeys?.keysToAdd ) {
      Object.entries(modifyKeys.keysToAdd).forEach( ( [key,val], index ) => {
        newSearchParams.set( key, val );
      } );
    }

    if ( params.orgId ) newSearchParams.set( 'org', params.orgId );

    return newSearchParams.toString();
  }, [ searchParams, params, modifyKeys ] );
}
